.khamPhaContent {
  width: calc(100% - 540px);
  position: relative;
  margin: 0 auto;
  padding-top: 52px;
  .container {
    padding: 32px 69px 92px 0px;
    .slick-slider {
      .slick-list {
        height: 200px;
        margin-left: 15px;
        .slick-slide {
          .slick-img {
            cursor: pointer;
            img {
              width: 96%;
              border-radius: 10px;
            }
          }
        }
      }
    }
    .ganday {
      padding: 8px;
      h3 {
        font-size: 20px;
        color: $text;
        padding: 10px;
        font-weight: 600;
      }
      .flex {
        display: flex;
        .item {
          padding: 10px;
          .musicItemView {
            cursor: pointer;
            position: relative;
            img {
              max-width: 100%;
              height: auto;
              border-radius: 5px;
              transition: 0.5s ease;
            }
            .musicItemOverlay {
              transition: 0.5s ease;
              color: $text;
              position: absolute;
              width: 100%;
              height: 100%;
              top: 0;
              .icon-overlay {
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                opacity: 0;
                svg {
                  font-size: 22px;
                  padding: 8px;
                }
                .playCircle {
                  font-size: 50px;
                }
              }
            }
            &:hover {
              .icon-overlay {
                opacity: 1;
              }
              .img {
                opacity: 0.4;
              }
            }
          }
          .music-title {
            color: $text;
            font-size: 16px;
            cursor: pointer;
            .title {
              padding: 10px 0;
              font-weight: 600;
            }
            .author {
              color: #ccc;
            }
          }
        }
      }
    }
    .section-footer {
      margin: 20px 0;
      .title {
        color: #ccc;
        display: flex;
        justify-content: center;
        font-size: 15px;
        font-weight: 600;
        text-transform: uppercase;
        margin: 20px 0;
      }
      .content {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        width: 100%;
        .footer-img {
          display: flex;
          justify-content: center;
          width: 12.5%;
          padding: 10px;
          .img {
            background-color: #fff;
            display: flex;
            justify-content: center;
            padding: 10px;
            border-radius: 10px;
            height: 40px;
            width: 100%;
            cursor: pointer;
            img {
              max-width: 90%;
              max-height: 80%;
            }
          }
        }
      }
    }
  }
}
